<template>
  <div ref="scrollWarpper" class="scroll-warpper">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from "better-scroll";
export default {
  name: "scroll",
  props: {
    data: {
      type: Array,
      default: [],
    },
  },
  data() {
    return {
      scroll: null,
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.initScroll();

      //  this.scroll.on('pullingUp', () => {
      //   console.log('Â§ÑÁêÜ‰∏äÊãâÂä†ËΩΩÊìç‰Ωú')
      //   setTimeout(() => {
      //     // ‰∫ãÊÉÖÂÅöÂÆåÔºåÈúÄË¶ÅË∞ÉÁî®Ê≠§ÊñπÊ≥ïÂëäËØâ better-scroll Êï∞ÊçÆÂ∑≤Âä†ËΩΩÔºåÂê¶Âàô‰∏äÊãâ‰∫ã‰ª∂Âè™‰ºöÊâßË°å‰∏ÄÊ¨°
      //     this.scroll.finishPullUp()
      //   }, 2000)
      // })
    });
  },
  watch: {
    data() {
      this.$nextTick(() => {
        this.scroll.refresh();
        //‰∏ãÊãâÂä†ËΩΩÂÆåÊàê
        this.scroll.finishPullUp();
      });
    },
  },
  methods: {
    //ÂàùÂßãÂåñ
    initScroll() {
      if (!this.$refs.scrollWarpper) return;
      this.scroll = new BScroll(this.$refs.scrollWarpper, {
        pullUpLoad: {
          threshold: 100,
          probeType: 3,
          click: true,
          tap: true,
          startY:0
        },
      });
    },
    //Êï∞ÊçÆÂèòÊõ¥Êó∂ÈáçÊñ∞ËÆ°ÁÆó
    refresh() {
      this.$refs.scrollWarpper && this.scroll.refresh();
    },
  },
};
</script>

